import Vue from "vue";
import Component from "vue-class-component";
import * as PIXI from "pixi.js";
import { Prop } from "vue-property-decorator";
import { buildSquare } from "@/utils/graphics-helper";
import { Key } from "ts-keycode-enum";

export interface ILevelConfig {
  gridRows: number;
  gridCols: number;
}
export interface IConfig {
  moveSpeed: number;
}

type TowerAction = "tower" | "clap" | "delete";

@Component
export default class GameCanvas extends Vue {
  @Prop() levelConfig: ILevelConfig;

  towerAction: TowerAction = "tower";

  app: PIXI.Application = null;

  grid: PIXI.Graphics[][] = [];
  towers: PIXI.Graphics[][] = [];

  mazer: PIXI.Graphics = null;

  mounted() {
    this.app = new PIXI.Application();
    this.app.renderer.backgroundColor = 0x7dd69d;

    this.$el.appendChild(this.app.view);

    this.setupGrid();
    this.setupHotkeys();
  }

  setupGrid() {
    const levelConfig = this.levelConfig;
    for (let row = 0; row < levelConfig.gridRows; row++) {
      this.grid.push([]);
      this.towers.push([]);

      for (let col = 0; col < levelConfig.gridCols; col++) {
        const x = col * 50 + 5;
        const y = row * 50 + 5;

        const tile = buildSquare(50, 0x2666cc, x, y, 2, 0xcccccc);
        tile.interactive = true;

        tile.addListener("click", () => this.tileClicked(row, col));

        this.app.stage.addChild(tile);

        this.grid[row].push(tile);
        this.towers[row].push(null);
      }
    }
  }

  setupHotkeys() {
    window.addEventListener("keydown", evt => {
      if (evt.which === Key.T) {
        this.towerAction = "tower";
      } else if (evt.which === Key.C) {
        this.towerAction = "clap";
      } else if (evt.which === Key.D) {
        this.towerAction = "delete";
      }
    });
  }

  tileClicked(row: number, col: number) {
    const x = col * 50 + 5;
    const y = row * 50 + 5;

    if (this.towerAction === "delete") {
      for (let r = row - 1; r <= row; r++) {
        for (let c = col - 1; c <= col; c++) {
          this.deleteTower(r, c);
        }
      }

      return;
    }
    if (row >= this.levelConfig.gridRows - 1 || col >= this.levelConfig.gridCols - 1) return;

    for (let r = row - 1; r <= row + 1; r++) {
      for (let c = col - 1; c <= col + 1; c++) {
        this.deleteTower(r, c);
      }
    }

    if (this.towerAction === "tower" || this.towerAction === "clap") {
      const color = this.towerAction === "tower" ? 0x777777 : 0xcc0000;
      const tower = buildSquare(100, color, x, y);
      this.app.stage.addChild(tower);
      this.towers[row][col] = tower;
    }
  }
  clearTowers() {
    const levelConfig = this.levelConfig;

    for (let row = 0; row < levelConfig.gridRows; row++) {
      for (let col = 0; col < levelConfig.gridCols; col++) {
        this.deleteTower(row, col);
      }
    }
  }
  deleteTower(row: number, col: number) {
    const levelConfig = this.levelConfig;
    if (row < 0 || row >= levelConfig.gridRows || col < 0 || col > levelConfig.gridCols) return;

    const tower = this.towers[row][col];
    if (tower) {
      this.app.stage.removeChild(tower);
      this.towers[row][col] = null;
    }
  }

  run() {
    const circle = new PIXI.Graphics();
    circle.beginFill(0xaa0000);
    circle.drawCircle(400, 500, 25);
    circle.endFill();

    this.app.stage.addChild(circle);

    let lastTime: number = null;
    let counter = 400;

    const loop = (time: number) => {
      if (lastTime === null) lastTime = time;

      const elapsedTime = time - lastTime;

      circle.y--;

      counter--;
      if (counter > 0) {
        requestAnimationFrame(loop);
      } else {
        this.app.stage.removeChild(circle);
      }
    };

    requestAnimationFrame(loop);
  }
}
