import * as PIXI from "pixi.js";

export function buildSquare(size: number, color: number, x: number, y: number, lineWidth?: number, lineColor?: number) {
  const square = new PIXI.Graphics();
  if (lineWidth) {
    square.lineStyle(lineWidth, lineColor);
  }

  square.beginFill(color);
  square.drawRect(x, y, size, size);
  square.endFill();

  return square;
}
